@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
* {
    box-sizing: border-box;
  }
  html {
    font-size: 62.5%;
    --black: rgb(17, 17, 17);
    --grey: rgb(22, 2, 36);
    --white: rgb(252, 252, 252);
    --bg: linear-gradient(
      45deg,
      #1f052b,
      #29053A
    );
  }
  body {
    background: linear-gradient(
      45deg,
      #1f052b,
      #29053A
    );
    background: var(--bg);
    background-color: rgb(22, 2, 36);
    background-color: var(--grey);
    color: rgb(252, 252, 252);
    color: var(--white);
    margin: 0;
    font-family: 'Kanit', sans-serif;
    font-size: 1.6rem;
  }
  :root {
    
    --gradient: linear-gradient(
        45deg,
        #51e5ac,
        #75eae4,
        #ebfc8b,
        #ea4798
    );
}

/*--------------------------------------------------------------
#                      Scroll To Top
--------------------------------------------------------------*/


.scroll-to-top img{
  width: 3vmax;
  height: auto;
}

.scroll-to-top {
  position: fixed;
  bottom: 1vmax;
  right: 3%;
  z-index: 1;
  cursor: pointer;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: all ease 0.4s;
}
.scroll-to-top:hover {
  -webkit-transform: scale(1);
          transform: scale(1);
}

@media (max-width: 800px) {
  .scroll-to-top img{
    width: 4.5vmax;
    height: auto;
  }
  .scroll-to-top {
    bottom: 1vmax;
    right: 3vmax;
    transition: all ease 0.4s;
  }
}



/*--------------------------------------------------------------
#                      Header Geral
--------------------------------------------------------------*/

.header {
  position: fixed;
  display: flex;
  width: 100%;
  height: 9vmax;
  transition: all ease 0.4s;
  z-index: 1;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

nav.black {
  background-color: rgba(18, 1, 32, 0.7);
}


/*--------------------------------------------------------------
#                       Logo Geral
--------------------------------------------------------------*/

.logo-header {
  margin-left: 7vmax;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
}

.logo {
  cursor: pointer;
  line-height: 9vmax;
  width: 9vmax;
  height: auto;
  transition: all ease 0.4s;
}
.logo:hover {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

/*--------------------------------------------------------------
#                        Menu Geral
--------------------------------------------------------------*/

.menu {
  position: absolute;
  display: flex;
  top: 0;
  right: 0;
  margin-right: 7vmax;
  font-size: 1.4vmax;
  line-height: 9vmax;
}


/*--------------------------------------------------------------
#                       Home Botão
--------------------------------------------------------------*/

.home {
  margin-left: 1vmax;
  margin-right: 1vmax;
  transition: all ease 0.2s;
  cursor: pointer;
}
.home:hover {
  color: rgb(252, 206, 79);
}

